import styles from './profile.module.css'
import { ProfileFooter } from '../../components/profile/profile-footer'
import { Link, useLocation } from 'react-router-dom'
import { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { CHANGE_PROFILE_STATE } from '../../services/actions/app'
import PropTypes from 'prop-types'

const NavItem = ({id, name, changeNav, type, path}) => {
  return (
    <Link to={{pathname: `/${path}`}} className={styles.link}>
      <div id={id} onClick={(e) => changeNav(e)} className={styles.navEl} style={type === 'primary' ? {color:'#F2F2F3'} : {color:'#8585AD'}}>{name}</div>
    </Link>
  )
}

export const ProfileNavigation = () => {
  const dispatch = useDispatch();
  const chosenNav = useSelector(state => state.app.chosenNav)
  const [navChosen, setNavChosen] = useState({});
  const location = useLocation();

  const changeModeF = () => {
    setNavChosen({
      profile: chosenNav === 'profile' ? 'primary' : 'secondary',
      orders: chosenNav === 'orders' ? 'primary' : 'secondary',
      logout: chosenNav === 'logout' ? 'primary' : 'secondary'
    })
  }

  useEffect(() => {
    const state = [...location.pathname].filter(el => el !== '/').slice(7).join('')
    dispatch({
      type: CHANGE_PROFILE_STATE,
      payload: {
        chosenNav: state ? state : 'profile'
      }
    })
  }, [])

  useEffect(() => {
    changeModeF();
  },[chosenNav])

  const changeNav = (el) => {
    dispatch({
      type: CHANGE_PROFILE_STATE,
      payload: {
        chosenNav: el.target.id
      }
    })
  }

  return (
    <section className={styles.navSection}>
      <nav>
        <NavItem id='profile' name='Профиль' type={navChosen.profile} changeNav={changeNav} path='profile'/>
        <NavItem id='logout' name='Выход' type={navChosen.logout} changeNav={changeNav} path='profile/logout'/>
      </nav>
      <ProfileFooter />
    </section>
  )
}

NavItem.propTypes = {
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  changeNav: PropTypes.func.isRequired,
  type: PropTypes.any,
  path: PropTypes.string,
}