import AppHeader from '../app-header/app-header';
import styles from './app.module.css';
import { Switch, Route} from 'react-router-dom'
import { BrowserRouter as Router, useLocation, useHistory } from 'react-router-dom';

import { HomePage } from '../../pages/home/home';
import { LoginPage } from '../../pages/login/login';
import { RegisterPage } from '../../pages/register/register';
import { PasswordForgotPage } from '../../pages/password-forgot/password-forgot';
import { PasswordResetPage } from '../../pages/password-reset/password-reset';
import { ProfilePage } from '../../pages/profile/profile';
import { ProfileLogoutPage } from '../../pages/profile/profile-logout';
import { NotFound404 } from '../../pages/not-found/not-found';
import { ProtectedRoute } from '../protected-rout/protected-route';
import Modal from '../modal/modal';
import IngredientsDetails from '../ingredient-details/ingredients-details';
import { getCookie } from '../../utils/cookie';
import { UPDATE_USER_AFTER_REGISTRATION } from '../../services/actions/user';

import { RESET_ITEM_TO_VIEW } from '../../services/actions/ingredient';
import { useDispatch } from 'react-redux';

const App = () => {
  const dispatch = useDispatch();
  const userNameCookie = getCookie('name');
  const userEmailCookie = getCookie('email');
  if(userNameCookie && userEmailCookie) {
    dispatch({
      type: UPDATE_USER_AFTER_REGISTRATION,
      payload: {
        name: userNameCookie,
        email: userEmailCookie,
      }
    })
  }
  const ModalSwitch = () => {
    const location = useLocation();
    const history = useHistory();
    let background = location.state && location.state.background;
   
    const handleModalClose = (e) => {
      if (e.target === e.currentTarget) {
        dispatch({
          type: RESET_ITEM_TO_VIEW,
         });
         history.goBack();
      }
      return
    };

    return (
      <div className={styles.body}>
        <AppHeader />
        <Switch location={background || location}>
          {/* Общие маршруты */}
          <Route exact path='/'>
            <HomePage />
          </Route>
          <Route exact path='/ingredients/:ingredientId'>
            <IngredientsDetails/>
          </Route>
          <Route exact path='/ingredients/:id'>
            <IngredientsDetails/>
          </Route>
          {/* Маршруты только для неавторизованных */}
          <ProtectedRoute ifNotAuth={true} exact path='/login'>
            <LoginPage exact path='/login'/>
          </ProtectedRoute>
          <ProtectedRoute ifNotAuth={true} exact path='/register'>
            <RegisterPage/>
          </ProtectedRoute>
          <ProtectedRoute ifNotAuth={true} exact path='/forgot-password'>
            <PasswordForgotPage />
          </ProtectedRoute>
          <ProtectedRoute ifNotAuth={true} exact path='/reset-password'>
            <PasswordResetPage />
          </ProtectedRoute>
          {/* Маршруты для авторизованных */}
          <ProtectedRoute exact path='/profile'>
            <ProfilePage />
          </ProtectedRoute>
          <ProtectedRoute exact path='/profile/logout'>
            <ProfileLogoutPage />
          </ProtectedRoute>

          {/* Page not found */}
          <Route>
            <NotFound404 />
          </Route>
        </Switch>
        {background && (
          <Route
            path='/ingredients/:ingredientId'
            children={
              <Modal closeModal={handleModalClose} >
                <IngredientsDetails />
              </Modal>
            }
          />
        )}
      </div>
    );
  };

  return (
    <ModalSwitch />
  )
}

export default App;