import { sendOrderAPI, checkResponse, handleErrorRequest } from '../../utils/api';

export const OPEN_MODAL_ORDER = 'OPEN_MODAL_ORDER' // opens modal with order info
export const CLOSE_MODAL_ORDER = 'CLOSE_MODAL_ORDER' // closes modal with order info

export const SEND_ORDER_SUCCESS = 'SEND_ORDER_SUCCESS' // sending order info to the server OK
export const SEND_ORDER_ERROR = 'SEND_ORDER_ERROR' // sending order info to the server ERROR

export function sendOrder( buns, restIngredients) {
  return function(dispatch) {
    const data = [buns._id, buns._id, restIngredients.map(el => el._id)]
    sendOrderAPI(data)
    .then(checkResponse)
    .then(data => {
      dispatch({
        type: SEND_ORDER_SUCCESS,
        id: data.order.number
      })
      dispatch({
        type: OPEN_MODAL_ORDER
      })
      return data
    })
    .catch(res => {
      handleErrorRequest(res, 'Ошибка в отправке заказа')
      dispatch({
        type: SEND_ORDER_ERROR
      })
      dispatch({
        type: OPEN_MODAL_ORDER
      })
    })
  }
}