import { Logo, BurgerIcon, ListIcon, ProfileIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import { useState, useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'
import PropTypes from 'prop-types'
import styles from './app-header.module.css'
import { useDispatch, useSelector } from 'react-redux';
import { APP_TAB_SWITCH } from '../../services/actions/app';

const HeaderItem = ({id, icon, name, changeAppTab, type, path}) => {
  return (
    <Link to={{pathname: `${path}`}} className={styles.link}>
      <div id={id} className={styles.element} onClick={(e) => changeAppTab(e)}>
        <div className={styles.icon}>{icon}</div>
        <div className={styles.name} style={type === 'primary' ? {color:'#F2F2F3'} : {color:'#8585AD'}}>{name}</div>
      </div>
    </Link>
  )
}

const AppHeader = () => {
  const dispatch = useDispatch();
  const location = useLocation();
  const isUserAuthorized = useSelector(state => state.user.userAuthorized);
  
  
  useEffect(() => {
    const state = [...location.pathname].filter(el => el !== '/').join('');
    dispatch({
      type: APP_TAB_SWITCH,
      tab: state ? state : 'constructor'
    })
  },[location])
  
  const { appTab } = useSelector(state => state.app);
  const userName = useSelector(state => state.user.user.name);

  const [modeChosen, setModeChosen] = useState({});

  const changeModeF = () => {
    const loginPages = ['login', 'profile', 'profileorders', 'profilelogout']
    setModeChosen({
      constructor: appTab === 'constructor' ? 'primary' : 'secondary',
      orders: appTab === 'orders' ? 'primary' : 'secondary',
      login: loginPages.indexOf(appTab) >= 0 ? 'primary' : 'secondary'
    })
  }

  useEffect(() => {
    changeModeF();
  },[appTab])

  const changeAppTab = (el) => {
    dispatch({
      type: APP_TAB_SWITCH,
      tab: el.currentTarget.id
    })
  }

  return (
    <header className={styles.header}>
      <nav className={`${styles.navigation}`}>
        <div className={styles.mode}>
          <HeaderItem id='constructor' icon={<BurgerIcon type={modeChosen.constructor}/>} name='Конструктор' type={modeChosen.constructor} changeAppTab={changeAppTab} path='/'/>
        </div>
        <div className={styles.logo}>
          <Logo />
        </div>
        <div className={styles.login}>
          <HeaderItem id='login' icon={<ProfileIcon type={modeChosen.login}/>} name={userName ? userName : 'Личный кабинет'} type={modeChosen.login} changeAppTab={changeAppTab} path={isUserAuthorized ? 'profile' : 'login'}/>
        </div>
      </nav>
    </header>
  )
}

HeaderItem.propTypes = {
  id: PropTypes.string.isRequired,
  icon: PropTypes.object.isRequired,
  name: PropTypes.string.isRequired,
  changeAppTab: PropTypes.func.isRequired,
  type: PropTypes.any,
  path: PropTypes.string
}

export default AppHeader