import styles from './order-details.module.css';
import { useSelector } from 'react-redux';

const OrderDetails = () => {
  const {id, message, orderFailed} = useSelector(state => state.sendOrder)
  return (
    <div className={styles.card}>
      <h1 className={styles.id}>{id}</h1>
      <div className={styles.title}>{orderFailed ? 'Произошла ошибка' : 'идентификатор заказа'}</div>
      <div className={styles.status}>
        {!orderFailed ? 
          <div className={styles.statusWrapper}>
            <svg width="48" height="48" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fillRule="evenodd" clipRule="evenodd" d="M43.3122 10.5959C44.1999 11.4172 44.2327 12.7806 43.3852 13.641L20.0519 37.3333C19.6272 37.7646 19.0371 38.0059 18.422 37.9999C17.8069 37.9939 17.222 37.741 16.8063 37.3016L4.58411 24.3785C3.7548 23.5016 3.81592 22.1392 4.72062 21.3354C5.62533 20.5316 7.03104 20.5908 7.86035 21.4677L18.4773 32.6934L40.1703 10.6667C41.0177 9.80621 42.4244 9.77451 43.3122 10.5959Z" fill="#F2F2F3"/>
            </svg>
          </div>
          :
          <div className={styles.orderFailed}>Заказ не может быть обработан :(</div>
        }
        
      </div>
      <div className={styles.message}>{message}</div>
      <div className={styles.subMessage}>{orderFailed ? 'Приносим извенения!' : 'Мы всегда рады вам!'}</div>
    </div>
  )
}

export default OrderDetails