import { Button } from '@ya.praktikum/react-developer-burger-ui-components'
import { sendLogoutUser } from '../../services/actions/user'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { setCookie } from '../../utils/cookie'
import styles from './profile.module.css'

export const ProfileLogout = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const state = useSelector(state => state.user.userAuthorized)
  const logout = (e) => {
    e.preventDefault();
    dispatch(sendLogoutUser());
    history.replace({pathname: '/'})
  }
  return (
    <div className={styles.btnLogout}>
      <Button type="primary" size="medium" htmlType='submit' onClick={logout}>
        Выйти из аккаунта
      </Button>
    </div>
  )
}
