import { 
  ADD_INGREDIENT_DETAILS,
  RESET_ITEM_TO_VIEW,
  GET_INGREGIENT_REQUEST,
  GET_INGREDIENT_SUCCESS,
  GET_INGREDIENT_FAILED,
 } from '../actions/ingredient'

const initialState = {
  fetchRequired: true,
  ingredientsRequested: false,
  ingredientsRequestedFailed: false,
  ingredientsDetails: {
    image: null,
    name: null,
    calories: null,
    proteins: null,
    fat: null,
    carbohydrates: null,
  },
}

export const ingredientsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_INGREDIENT_DETAILS: {
      return {
        ...state,
        fetchRequired: false,
        ingredientsDetails: {
          image: action.payload.image,
          name: action.payload.name,
          calories: action.payload.calories,
          proteins: action.payload.proteins,
          fat: action.payload.fat,
          carbohydrates: action.payload.carbohydrates
        }
      }
    }
    case RESET_ITEM_TO_VIEW: {
      return {
        ...state,
        fetchRequired: true,
        ingredientsDetails: {
          image: null,
          name: null,
          calories: null,
          proteins: null,
          fat: null,
          carbohydrates: null,
        }
      }
    }
    case GET_INGREGIENT_REQUEST: {
      return {
        ...state,
        ingredientsRequested: true,
      }
    }
    case GET_INGREDIENT_SUCCESS: {
      return {
        ...state,
        ingredientsRequested: false,
        ingredientsRequestedFailed: false,
        fetchRequired: false,
        ingredientsDetails: {
          image: action.item.image_large,
          name: action.item.name,
          calories: action.item.calories,
          proteins: action.item.proteins,
          fat: action.item.fat,
          carbohydrates: action.item.carbohydrates
        }
      }
    }
    case GET_INGREDIENT_FAILED: {
      return {
        ...state,
        ingredientsRequested: false,
        ingredientsRequestedFailed: true,
      }
    }
    default: {
      return state;
    }
  }
}