import { Input, EmailInput, PasswordInput, Button  } from '@ya.praktikum/react-developer-burger-ui-components';
import styles from './input-form.module.css'
import { useState, useEffect } from 'react'
import { useSelector } from 'react-redux';

import { Link } from 'react-router-dom';

export const InputForm = ({title, name, email, password, emailCode, btnName, btnSubmit, redirectInfo}) => {

  const user = useSelector(state => state.user);

  const [valueName, setValueName] = useState('');
  const [valueEmail, setValueEmail] = useState('');
  const [valuePassword, setValuePassword] = useState('');
  const [valueCode, setValueCode] = useState('');

  useEffect(() => {
    if (!user.user.name) return
    setValueName(user.user.name)
  },[user.user.name])
  useEffect(() => {
    if (!user.user.email) return
    setValueEmail(user.user.email)
  },[user.user.email])
  useEffect(() => {
    if (!user.user.password) return
    setValuePassword(user.user.password)
  },[user.user.password])

  return (
    <div className={styles.wrapper}>
      <form>
        <div className={styles.title}>{title}</div>
        {
          name ?
          <div className={styles.formItem} >
            <Input
              type={'text'}
              placeholder={name.placeholder}
              value={valueName}
              onChange={e => setValueName(e.target.value)}
              name={'name'}
              error={false}
              errorText={'Ошибка'}
              size={'default'}
            />
          </div> :
          ''
        }
        {
          email ?
          <div className={styles.formItem}>
            <EmailInput 
              value={valueEmail}
              name={'email'}
              placeholder={email.placeholder}
              onChange={e => setValueEmail(e.target.value)}
              size={'default'}
            />
          </div> : 
          ''
        }
        {
          password ?
          <div className={styles.formItem}>
            <PasswordInput
              value={valuePassword}
              name={'password'}
              placeholder={password.placeholder}
              onChange={e => setValuePassword(e.target.value)}
              size={'default'}
            />
          </div> :
          ''
        }
        {
          emailCode ? 
          <div className={styles.formItem}>
            <Input
              type={'text'}
              placeholder={'Введите код из письма'}
              value={valueCode}
              onChange={e => setValueCode(e.target.value)}
              name={'code'}
              error={false}
              errorText={'Ошибка'}
              size={'default'}
            />
          </div> :
          ''
        }
        {
          btnName ?
          <div className={styles.btn}>
            <Button htmlType='submit'
              type="primary"
              size="medium"
              onClick={btnSubmit}
            >
              {btnName}
            </Button>
          </div> : 
          ''
        }
        
      </form>
      <div className={styles.redirect}>
        {redirectInfo.map((el) => {
          return(
            <div key={el.id}>
              <span>{el.question}</span>
              <Link to={el.link}>{el.answer}</Link>
            </div>
          )
        })}
      </div>
    </div>
  )
}
