import { v4 as uuidv4 } from 'uuid'

export const ADD_TO_CONSTRUCTOR = 'ADD_TO_CONSTRUCTOR';
export const REMOVE_FROM_CONSTRUCTOR = 'REMOVE_FROM_CONSTRUCTOR';
export const SHIFT_INSIDE_CONSTRUCTOR = 'SHIFT_INSIDE_CONSTRUCTOR';

export const addToConstructor = (item) => {
  return {
    type: ADD_TO_CONSTRUCTOR,
    payload: {
      ...item,
      id: uuidv4()
    }
  }
}