import { 
  SEND_FORGOT_PASSWORD_SUCCESS,
  SEND_FORGOT_PASSWORD_FAILED,
  SEND_RESET_PASSWORD_SUCCESS,
  SEND_RESET_PASSWORD_FAILED,
} from '../actions/password'

const initialState = {
  forgotRequestSuccess: false,
  forgotRequestFailed: false,
  resetRequestSuccess: false,
  resetRequestFailed: false,
}

export const passwordReducer = (state = initialState, action) => {
  switch(action.type) {
    case SEND_FORGOT_PASSWORD_SUCCESS: {
      return {
        ...state,
        forgotRequestSuccess: true,
      }
    }
    case SEND_FORGOT_PASSWORD_FAILED: {
      return {
        ...state,
        forgotRequestSuccess: false,
      }
    }
    case SEND_RESET_PASSWORD_SUCCESS: {
      return {
        ...state,
        resetRequestSuccess: true,
        forgotRequestSuccess: false,
      }
    }
    case SEND_RESET_PASSWORD_FAILED: {
      return {
        ...state,
        resetRequestSuccess: false,
      }
    }
    default: {
      return {
        ...state
      }
    }
  }
}