import {
  sendRegisterRequestAPI,
  sendLoginUserAPI,
  sendGetUserAPI,
  sendLogoutUserAPI,
  sendUpdateUserAPI,
} from '../../utils/api'

import { 
  checkResponse,
  handleErrorRequest,
} from '../../utils/api'

import { 
  ALERT_MESSAGE_ERROR_REGISTER_REQUEST,
  ALERT_MESSAGE_ERROR_LOGIN_REQUEST,
  ALERT_MESSAGE_ERROR_GET_USER_REQUEST,
  ALERT_MESSAGE_ERROR_LOGOUT_USER_REQUEST,
  ALERT_MESSAGE_ERROR_UPDATE_USER_REQUEST,
} from '../../utils/api'

import {
  setCookie,
} from '../../utils/cookie';

export const SEND_REGISTER_SUCCESS = 'SEND_REGISTER_SUCCESS'; // request to server succeded
export const SEND_REGISTER_FAILED = 'SEND_REGISTER_FAILED'; // request to server failed

export const UPDATE_USER_AFTER_REGISTRATION = 'REGISTER_USER' // updating user info after registration

export const LOGIN_USER_SUCCESS = 'LOGIN_USER_SUCCESS' // login was successfull
export const LOGIN_USER_FAILED = 'LOGIN_USER_FAILED' // login failed

export const GET_USER_DATA_SUCCESS = 'GET_USER_DATA_SUCCESS' // get user data from server via accessToken
export const GET_USER_DATA_FAILED = 'GET_USER_DATA_FAILED' // get user data from server via accessToken failed

export const SEND_LOGOUT_USER = 'SEND_LOGOUT_USER' //user information cleared

export const SEND_UPDATE_USER = 'SEND_UPDATE_USER' // updating user info

export function sendRegisterRequest(name, email, password) {
  return function(dispatch) {
    sendRegisterRequestAPI(name, email, password)
    .then(checkResponse)
    .then(data => {
      const authToken = data.accessToken.split('Bearer ')[1];
      const refreshToken = data.refreshToken;
      setCookie('accessToken', authToken);
      localStorage.setItem('refreshToken', refreshToken);
      setCookie('name', data.user.name);
      setCookie('email', data.user.email);
      dispatch({
        type: SEND_REGISTER_SUCCESS,
      })
      dispatch({
        type: UPDATE_USER_AFTER_REGISTRATION,
        payload: data.user,
      })
      return data
    })
    .catch(res => {
      handleErrorRequest(res, ALERT_MESSAGE_ERROR_REGISTER_REQUEST)
      dispatch({
        type: SEND_REGISTER_FAILED
      })
    })
  }
}

export function sendLoginUser (email, password) {
  return function(dispatch) {
    sendLoginUserAPI(email, password)
    .then(checkResponse)
    .then(data => {
      const authToken = data.accessToken.split('Bearer ')[1];
      const refreshToken = data.refreshToken;
      localStorage.setItem('refreshToken', refreshToken);
      setCookie('accessToken', authToken);
      setCookie('name', data.user.name);
      setCookie('email', data.user.email);
      dispatch({
        type: LOGIN_USER_SUCCESS,
        payload: data.user,
      })
      return data
    })
    .catch(res => {
      handleErrorRequest(res, ALERT_MESSAGE_ERROR_LOGIN_REQUEST)
      dispatch({
        type: LOGIN_USER_FAILED
      })
    })
  }
}

export function sendGetUser () {
  return function(dispatch) {
    sendGetUserAPI()
    .then(data => {
      dispatch({
        type: GET_USER_DATA_SUCCESS,
        payload: data.user,
      })
      setCookie('name', data.user.name);
      setCookie('email', data.user.email);
      return data
    })
    .catch(res => {
      handleErrorRequest(res, ALERT_MESSAGE_ERROR_GET_USER_REQUEST)
      dispatch({
        type: GET_USER_DATA_FAILED
      })
    })
  }
}


export function sendUpdateUser (name, email) {
  return function(dispatch) {
    sendUpdateUserAPI(name, email)
    .then(data => {
      dispatch({
        type: SEND_UPDATE_USER,
        payload: {
          name: name,
          email: email,
        }
      })
      setCookie('name', data.user.name);
      setCookie('email', data.user.email);
      return data
    })
    .catch(res => {
      handleErrorRequest(res, ALERT_MESSAGE_ERROR_UPDATE_USER_REQUEST)
    })
  }
}

export function sendLogoutUser () {
  return function(dispatch) {
    sendLogoutUserAPI()
    .then(checkResponse)
    .then(data => {
      setCookie('name', null, { expires: -1 })
      setCookie('email', null, { expires: -1 })
      setCookie('accessToken', null, { expires: -1 })
      localStorage.removeItem('refreshToken')
      dispatch({
        type: SEND_LOGOUT_USER,
      })
      alert('Вы успешно вышли из аккаунта')
      return data
    })
    .catch(res => {
      handleErrorRequest(res, ALERT_MESSAGE_ERROR_LOGOUT_USER_REQUEST)
    })
  }
}