import { sendForgotPasswordAPI, sendResetPasswordAPI, checkResponse, handleErrorRequest, ALERT_MESSAGE_ERROR_FORGOT_REQUEST, ALERT_MESSAGE_ERROR_RESET_REQUEST } from '../../utils/api'


export const SEND_FORGOT_PASSWORD_SUCCESS = 'SEND_FORGOT_PASSWORD_SUCCESS'; // request to server succeded
export const SEND_FORGOT_PASSWORD_FAILED = 'SEND_FORGOT_PASSWORD_FAILED'; // request to server failed

export const SEND_RESET_PASSWORD_SUCCESS = 'SEND_RESET_PASSWORD_SUCCESS'; // request to server succeded
export const SEND_RESET_PASSWORD_FAILED = 'SEND_RESET_PASSWORD_FAILED'; // request to server failed

export function sendForgotPassword(mail) {
  return function(dispatch) {
    sendForgotPasswordAPI(mail)
    .then(checkResponse)
    .then(data => {
      dispatch({
        type: SEND_FORGOT_PASSWORD_SUCCESS,
      })
      return data
    })
    .catch(res => {
      handleErrorRequest(res, ALERT_MESSAGE_ERROR_FORGOT_REQUEST)
      dispatch({
        type: SEND_FORGOT_PASSWORD_FAILED
      })
    })
  }
}

export function sendResetPassword(password, code) {
  return function(dispatch) {
    sendResetPasswordAPI(password, code)
    .then(checkResponse)
    .then(data => {
      dispatch({
        type: SEND_RESET_PASSWORD_SUCCESS,
      })
      return data
    })
    .catch(res => {
      handleErrorRequest(res, ALERT_MESSAGE_ERROR_RESET_REQUEST)
      dispatch({
        type: SEND_RESET_PASSWORD_FAILED
      })
    })
  }
}