import {
  GET_INGREGIENTS_REQUEST,
  GET_INGREDIENTS_SUCCESS,
  GET_INGREDIENTS_FAILED,
  REDUCE_APP_COUNTER,
  APP_TAB_SWITCH,
  SHOW_HOMEPAGE,
  INGREDIENTS_TAB_SWITCH,
  CHANGE_PROFILE_STATE,
} from '../actions/app'

const initialState = {
  ingredients: [],
  ingredientsRequest: false,
  itemsFailed: false,
  ingredientsChosen: [],

  appCount: 4,
  homePageToShow: false,

  appTab: 'constructor',
  ingredientsTab: 'buns',

  isModalOpened: false,

  chosenNav: 'profile',
}

export const app = (state = initialState, action) => {
  switch (action.type) {
    case GET_INGREGIENTS_REQUEST: {
      return {
        ...state,
        ingredientsRequest: true
      };
    }
    case GET_INGREDIENTS_SUCCESS: {
      return {
        ...state,
        ingredients: action.items,
        ingredientsRequest: false,
        itemsFailed: false
      }
    }
    case GET_INGREDIENTS_FAILED: {
      return {
        ...state,
        ingredientsRequest: false,
        itemsFailed: true
      }
    }
    case REDUCE_APP_COUNTER: {
      return {
        ...state,
        appCount: state.appCount - 1
      }
    }
    case APP_TAB_SWITCH: {
      return {
        ...state,
        appTab: action.tab
      }
    }
    case SHOW_HOMEPAGE: {
      return {
        ...state,
        homePageToShow: true
      }
    }
    case INGREDIENTS_TAB_SWITCH: {
      return {
        ...state,
        ingredientsTab: action.ingredient
      }
    }
    case CHANGE_PROFILE_STATE: {
      return {
        ...state,
        chosenNav: action.payload.chosenNav
      }
    }
    default: {
      return state;
    }
  }
}