import { Input, PasswordInput, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styles from './profile.module.css';
import { sendUpdateUser } from '../../services/actions/user';

export const ProfileInfo = () => {
  const user = useSelector(state => state.user.user)

  const dispatch = useDispatch();


  const [valueName, setValueName] = useState('');
  const [valueLogin, setValueLogin] = useState('');
  const [valuePassword, setValuePassword] = useState('');

  const [valueFormState, setValueFormState] = useState(false)

  useEffect(() => {
    setValueName(user.name)
    setValueLogin(user.email)
    return
  },[])

  useEffect(() => {
    if (valueName !== user.name || valueLogin !== user.email || valuePassword !== '') {
      setValueFormState(true)
    } else {
      setValueFormState(false)
    }
  }, [valueName, valueLogin, valuePassword])

  const reject = () => {
    setValueName(user.name);
    setValueLogin(user.email);
    setValuePassword('');
  }
  const save = (e) => {
    e.preventDefault();
    if(valueName === '' || valueLogin === '') {
      alert('Поля личной информации должны быть заполнены корректно')
      return
    }
    dispatch(sendUpdateUser(valueName, valueLogin))
    alert('Данные успешно обновлены!')
  }
  return (
    <div className={styles.formWrapper}>
      <div className={styles.formItem} >
        <Input
          type={'text'}
          placeholder={'Имя'}
          value={valueName}
          onChange={e => setValueName(e.target.value)}
          name={'name'}
          error={false}
          errorText={'Ошибка'}
          size={'default'}
          icon='EditIcon'
        />
      </div>
      <div className={styles.formItem} >
        <Input
          type={'text'}
          placeholder={'Логин'}
          value={valueLogin}
          onChange={e => setValueLogin(e.target.value)}
          name={'email'}
          error={false}
          errorText={'Ошибка'}
          size={'default'}
          icon='EditIcon'
        />
      </div>
      <div className={styles.formItem} >
        <PasswordInput
          value={valuePassword}
          name={'password'}
          placeholder={'Пароль'}
          onChange={e => setValuePassword(e.target.value)}
          size={'default'}
          icon='EditIcon'
        />
      </div>
      <div className={styles.btnUser}>
        <Button htmlType='submit' type={valueFormState ? 'primary' : 'secondary'} onClick={valueFormState ? reject : undefined} style={valueFormState ? {color: 'white'} : {color: '#707079d3'}}>Отмена</Button>
        <Button htmlType='submit' type={valueFormState ? 'primary' : 'secondary'} onClick={valueFormState ? save : undefined} style={valueFormState ? {color: 'white'} : {color: '#707079d3'}}>Сохранить</Button>
      </div>
    </div>
  )
}
