export const inputReturns = () => {
  const name = document.querySelector('input[name="name"]')?.value;
  const email = document.querySelector('input[name="email"]')?.value;
  const password = document.querySelector('input[name="password"]')?.value;
  const code = document.querySelector('input[name="code"]')?.value;
  const parentElementEmail = document.querySelector('input[name="email"]')?.parentElement.parentElement;
  const parentElementPassword = document.querySelector('input[name="password"]')?.parentElement.parentElement;
  const parentElementCode = document.querySelector('input[name="code"]')?.parentElement.parentElement;
  return {name, email, password, code, parentElementEmail, parentElementPassword, parentElementCode}
}