import {
  OPEN_MODAL_ORDER,
  CLOSE_MODAL_ORDER,
  SEND_ORDER_SUCCESS,
  SEND_ORDER_ERROR
} from '../actions/order'

const initialState = {
  isModalOpened: false,
  id: null,
  message: '',
  orderSend: false,
  orderFailed: false,
}

export const sendOrder = (state = initialState, action) => {
  switch (action.type) {
    case OPEN_MODAL_ORDER: {
      return {
        ...state,
        orderSend: true,
        isModalOpened: !state.isModalOpened
      }
    }
    case SEND_ORDER_SUCCESS: {
      return {
        ...state,
        id: action.id,
        message: 'Ваш заказ в обработке',
        orderSend: false
      }
    }
    case SEND_ORDER_ERROR: {
      return {
        ...state,
        id: '',
        message: 'В данный момент невозможно отправить заказ',
        orderFailed: true,
        orderSend: false
      }
    }
    case CLOSE_MODAL_ORDER: {
      return {
        ...state,
        orderFailed: false,
        isModalOpened: !state.isModalOpened
      }
    }
    default: {
      return state;
    }
  }
}