import { InputForm } from '../../components/input-forms/input-form'
import { useDispatch, useSelector } from 'react-redux';
import { sendForgotPassword } from '../../services/actions/password';
import { Redirect, useHistory } from 'react-router-dom';

export const PasswordForgotPage = () => {
  const userAuthorized = useSelector(state => state.user.userAuthorized);
  const dispatch = useDispatch();
  const state = useSelector(state => state.password.forgotRequestSuccess);
  const history = useHistory();
  const submit = (e) => {
    e.preventDefault();
    const email = document.querySelector('input[name="email"]').value;
    const parentElement = document.querySelector('input[name="email"]').parentElement.parentElement;
    if([...parentElement.children].length === 2 || email === '') {
      alert('Пожалуйста, введите корректный email')
      return
    }
    dispatch(sendForgotPassword(email))
  }
  if (state) {
    history.replace({pathname: '/reset-password'})
  }
  if (userAuthorized) {
    return (
      <Redirect
        to={{
          pathname: '/'
        }}
      />
    );
  }
  return (
    <InputForm
      title='Восстановление пароля'
      email={{placeholder: 'Укажите e-mail'}}
      btnName='Восстановить'
      btnSubmit={submit}
      redirectInfo={
        [
          {
            id: 1,
            question: 'Вспомнили пароль?', 
            answer: 'Войти',
            link: '/login'
          }
        ]
      }
    />
  )
}
