import { useDispatch, useSelector } from 'react-redux'
import { useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import { getIngredient } from '../../services/actions/ingredient'

import styles from './ingredients-details.module.css'

const IngredientsDetails = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const { fetchRequired } = useSelector(state => state.ingredients);
  let data = useSelector(state => state.ingredients.ingredientsDetails);

  useEffect(() => {
    if(fetchRequired) {
      const id = location.pathname.slice(13)
      dispatch(getIngredient(id))
    } else {
      return
    }
  }, [dispatch])

  return (
    <div className={styles.card}>
      <h1 className={styles.header}>Детали ингредиента</h1>
      <div className={styles.img}>
        <img className={styles.img} src={data.image} alt={`Изображение ${data.name}`}></img>
      </div>
      <h2 className={styles.name}>{data.name}</h2>
      <section className={styles.section}>
        <div className={styles.nutritionItem}>
          <p>Калории, ккал</p>
          <p>{data.calories}</p>
        </div>
        <div className={styles.nutritionItem}>
          <p>Белки, г</p>
          <p>{data.proteins}</p>
        </div>
        <div className={styles.nutritionItem}>
          <p>Жиры, г</p>
          <p>{data.fat}</p>
        </div>
        <div className={styles.nutritionItem}>
          <p>Углеводы, г</p>
          <p>{data.carbohydrates}</p>
        </div>
      </section>
    </div>
  )
}

export default IngredientsDetails