import {
  ADD_TO_CONSTRUCTOR,
  REMOVE_FROM_CONSTRUCTOR,
  SHIFT_INSIDE_CONSTRUCTOR
} from '../actions/burger-constructor'

const initialState = {
  buns: null,
  restIngredients: []
}

export const burgerConstructor = (state = initialState, action) => {
  switch (action.type) {
    case ADD_TO_CONSTRUCTOR: {
      if (action.payload.type === 'bun') {
        return {
          ...state,
          buns: action.payload
        } 
      } else {
        if(!state.buns) {
          alert ('Для начала добавьте булку!')
          return state
        }
        return {
          ...state,
          restIngredients: [...state.restIngredients, action.payload]
        }
      }
    }
    case REMOVE_FROM_CONSTRUCTOR: {
      const chosenItem = action.item.currentTarget.parentElement.parentElement.parentElement.parentElement.id
      return {
        ...state,
        restIngredients: state.restIngredients.filter(el => el.id !== chosenItem)
      }
    }
    case SHIFT_INSIDE_CONSTRUCTOR: {
      return {
        ...state,
        restIngredients: action.optional
      }
    }
    default: {
      return state;
    }
  }
}