import { Counter } from '@ya.praktikum/react-developer-burger-ui-components'
import { CurrencyIcon } from '@ya.praktikum/react-developer-burger-ui-components'
import { useDispatch, useSelector } from 'react-redux';
import { ADD_INGREDIENT_DETAILS } from '../../services/actions/ingredient';
import { useDrag } from 'react-dnd'
import { Link, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types'
import styles from './item.module.css'

const IngredientItem = ({item}) => {
  const dispatch = useDispatch();
  const location = useLocation();
  const ingredientId = item['_id']
  const { buns, restIngredients} = useSelector(state => state.burgerConstructor)
  
  const [{opacity}, dragRef] = useDrag({
    type: item.type === 'bun' ? 'bun' : 'notBun',
    item: item,
    collect: monitor => ({
      opacity: monitor.isDragging() ? 0.2 : 1
    })
  });

  const countIngredient = () => {
    if (item.type === 'bun') {
      if (!buns) return 0
      return (item._id === buns._id ? 2 : 0)
    }
    if (item.type !== 'bun') {
      if (restIngredients.length === 0) return 0;
      return restIngredients.filter((el) => el._id === item._id).length;
    }
  }

  const sendItemToStore = () => {
    dispatch({
      type: ADD_INGREDIENT_DETAILS,
      payload: {
        image: item.image_large,
        name: item.name,
        calories: item.calories,
        proteins: item.proteins,
        fat: item.fat,
        carbohydrates: item.carbohydrates
      }
    })
  }

  return (
    <Link
      key={ingredientId}
      to={{
        pathname: `/ingredients/${ingredientId}`,
        state: {
          background: location,
        }
      }}
      className={styles.link}
    >
      <div ref={dragRef} className={styles.item} style={{opacity}} onClick={sendItemToStore}>
        <img src={item.image} alt={`Изображение ${item.name}`} className={styles.img}></img>
        <Counter className={styles.counter} count={countIngredient()} size={countIngredient() < 10 ? 'default' : 'small'}/>
        <div className={styles.priceContainer}>
          <span className={styles.price}>{item.price}
            <CurrencyIcon type='primary'/>
          </span>
        </div>
        <p className={styles.name}>{item.name}</p>
      </div>
    </Link>
  )
}

IngredientItem.propTypes = {
  item: PropTypes.object.isRequired
}


export default IngredientItem