import { getIngredientsAPI, handleErrorRequest, ALERT_MESSAGE_ERROR_ALL_INGREDIENTS_REQUEST } from '../../utils/api'

export const GET_INGREGIENTS_REQUEST = 'GET_INGREGIENTS_REQUEST'; // sending request to server to obtain all ingredients
export const GET_INGREDIENTS_SUCCESS = 'GET_INGREDIENTS_SUCCESS'; // request to server succeded
export const GET_INGREDIENTS_FAILED = 'GET_INGREDIENTS_FAILED'; // request to server failed

export const REDUCE_APP_COUNTER = 'REDUCE_APP_COUNTER'; // for the first time of App mount (when no ingredients were obtained yet)
export const SHOW_HOMEPAGE = 'SHOW_HOMEPAGE'; // when counts to 0 or 1 in strict/nonstrict mode homepage will show

export const APP_TAB_SWITCH = 'APP_TAB_SWITCH'; // set tab in main navigation
export const INGREDIENTS_TAB_SWITCH = 'INGREDIENTS_TAB_SWITCH' // set tab in ingredients navigarion

export const CHANGE_PROFILE_STATE = 'CHANGE_PROFILE_STATE' //swith between different options inside profile navigation menu

export function getItems() {
  return function(dispatch) {
    dispatch({
      type: GET_INGREGIENTS_REQUEST
    });
    getIngredientsAPI()
    .then(data => {
      if (data && data.success) {
        dispatch({
          type: GET_INGREDIENTS_SUCCESS,
          items: data.data
        });
      }
    })
    .catch(err => {
      handleErrorRequest(err, ALERT_MESSAGE_ERROR_ALL_INGREDIENTS_REQUEST)
      dispatch({
        type: GET_INGREDIENTS_FAILED
      });
    });
  };
}