import BurgerIngredients from '../../components/burger-ingredients/burger-ingredients';
import BurgerConstructor from '../../components/burger-constructor/burger-constructor';
import styles from './home.module.css';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { REDUCE_APP_COUNTER, SHOW_HOMEPAGE } from '../../services/actions/app';
import { getItems } from '../../services/actions/app';

import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

export const HomePage = () => {
  const dispatch = useDispatch();
  const {ingredients, ingredientsRequest, appCount, homePageToShow} = useSelector(state => state.app)
 
  useEffect(() => {
    if(ingredients.length === 0) dispatch(getItems());
  },
  [dispatch]);

  useEffect(() => {
    if (appCount > 1) {
      setTimeout(() => dispatch({
        type: REDUCE_APP_COUNTER
      }), 1000);
    } else {
      dispatch({
        type: SHOW_HOMEPAGE
      })
    };
    return
  },[dispatch, appCount])

  return (
    <>
      {(ingredients.length && homePageToShow && !ingredientsRequest) ? 
      <DndProvider backend={HTML5Backend}>
        <main className={styles.main}>
          <BurgerIngredients/>
          <BurgerConstructor/>
        </main>
      </DndProvider> : 
      <div className={styles.preloader}>Получаем данные с межгалактического сервера...</div>}
    </>
  )
}