import {
  OPEN_MODAL_INGREDIENT,
  CLOSE_MODAL_INGREDIENT,
} from '../actions/modal'

const initialState = {
  isModalOpened: false,
  data: null,
}

export const changeIngredientModal = (state = initialState, action) => {
  switch (action.type) {
    case OPEN_MODAL_INGREDIENT: {
      return {
        ...state,
        isModalOpened: !state.isModalOpened
      }
    }
    case CLOSE_MODAL_INGREDIENT: {
      return {
        ...state,
        isModalOpened: !state.isModalOpened
      }
    }
    default: {
      return state;
    }
  }
}