import { getIngredientsAPI, handleErrorRequest, ALERT_MESSAGE_ERROR_INGREDIENT_REQUEST } from '../../utils/api'

export const ADD_INGREDIENT_DETAILS = 'ADD_INGREDIENT_DETAILS'
export const RESET_ITEM_TO_VIEW = 'RESET_ITEM_TO_VIEW' // change modal state

export const GET_INGREGIENT_REQUEST = 'GET_INGREGIENT_REQUEST'; // sending request to server to obtain all ingredients
export const GET_INGREDIENT_SUCCESS = 'GET_INGREDIENT_SUCCESS'; // request to server succeded
export const GET_INGREDIENT_FAILED = 'GET_INGREDIENT_FAILED'; // request to server failed

export const getIngredient = (id) => {
  return function(dispatch) {
    dispatch({
      type: GET_INGREGIENT_REQUEST
    });
    getIngredientsAPI()
    .then(data => {
      if (data && data.success) {
        const item = data.data.filter((el) => el._id === id)[0]
        dispatch({
          type: GET_INGREDIENT_SUCCESS,
          item: item
        });
      }
    })
    .catch(err => {
      handleErrorRequest(err, ALERT_MESSAGE_ERROR_INGREDIENT_REQUEST)
      dispatch({
        type: GET_INGREDIENT_FAILED
      });
    });
  };
}