import { useSelector } from 'react-redux'
import { Route, Redirect, useLocation } from 'react-router-dom';

export const ProtectedRoute = ({ ifNotAuth = false, ...rest }) => {
  const user = useSelector(state => state.user.userAuthorized)
  const location = useLocation();

  if(ifNotAuth && !user) {
    return <Route {...rest} />
  }

  if (!ifNotAuth && !user) {
    return (
      <Redirect to={{
        pathname: 'login',
        state: { from: location },
      }}
      />
    )
  }
  if (!ifNotAuth && user) {
    return (
      <Route {...rest} />
    )
  }
  return <Route {...rest} />
}