import { InputForm } from '../../components/input-forms/input-form';
import { useDispatch, useSelector } from 'react-redux';
import { sendResetPassword } from '../../services/actions/password';
import { Redirect, useHistory } from 'react-router-dom';
import { inputReturns } from '../../utils/inputReturns';

export const PasswordResetPage = () => {
  const userAuthorized = useSelector(state => state.user.userAuthorized);
  const dispatch = useDispatch();
  const state = useSelector(state => state.password.resetRequestSuccess);
  const wasEmailSend = useSelector(state => state.password.forgotRequestSuccess);
  const history = useHistory();
  const submit = (e) => {
    e.preventDefault();
    const { password, code, parentElementPassword, parentElementCode } = inputReturns();
    
    if([...parentElementPassword.children].length === 2 || [...parentElementCode.children].length === 2 || password === '' || code === '') {
      alert('Пожалуйста, введите корректные данные')
      return
    }
    dispatch(sendResetPassword(password, code))
   }
  if (state) {
    history.replace({pathname: '/login'})
    alert('Пароль успешно обновлен!')
  }
  
  if (userAuthorized) {
    return (
      <Redirect
        to={{
          pathname: '/'
        }}
      />
    );
  }
  if (!wasEmailSend) {
    return (
      <Redirect
        to={{
          pathname: '/'
        }}
      />
    );
  }
  return (
    <InputForm
      title='Восстановление пароля'
      password={{placeholder: 'Введите новый пароль'}}
      emailCode={true}
      btnName='Сохранить'
      btnSubmit={submit}
      redirectInfo={
        [
          {
            id: 1,
            question: 'Вспомнили пароль?', 
            answer: 'Войти',
            link: '/login'
          }
        ]
      }
    />
  )
}