import { Tab } from '@ya.praktikum/react-developer-burger-ui-components'
import IngredientItem from './item'
import PropTypes from 'prop-types'
import styles from './burger-ingredients.module.css'
import ingredientsPropsTypes from '../../utils/prop-types'
import { useDispatch, useSelector } from 'react-redux'
import { INGREDIENTS_TAB_SWITCH } from '../../services/actions/app'

const IngredientsNavigation = () => {

  const dispatch = useDispatch();
  const { ingredientsTab } = useSelector(state => state.app)

  const setCurrentIngredient = (e) => {
    dispatch({
      type: INGREDIENTS_TAB_SWITCH,
      ingredient: e
    })
    
    const scrollTo = document.getElementById(e);
    scrollTo.scrollIntoView({behavior: 'smooth'});

  }
  return (
    <nav className={styles.navigation} id='navId'>
      <Tab value="buns" active={ingredientsTab === 'buns'} onClick={(e) => setCurrentIngredient(e)}>
        Булки
      </Tab>
      <Tab value="sauses" active={ingredientsTab === 'sauses'} onClick={(e) => setCurrentIngredient(e)}>
        Соусы
      </Tab>
      <Tab value="mains" active={ingredientsTab === 'mains'} onClick={(e) => setCurrentIngredient(e)}>
        Начинки
      </Tab>
    </nav>
  )
}

const IngredientsType = ({ingredientId, header, ingredients}) => {
  return (
    <>
      <h3 id={ingredientId} className={styles.headerIngredient}>{header}</h3>
      <div className={styles.ingredient}>
        {
          ingredients.map((el) => 
          <IngredientItem key={el._id} item={el}/>)
        }
      </div>
    </>
  )
}

const BurgerIngredients = () => {
  const dispatch = useDispatch();
  const { ingredientsTab, ingredients } = useSelector(state => state.app);


  function changeView() {
    const navId = document.getElementById('navId').getBoundingClientRect().bottom;
    const sauses = document.getElementById('sauses');
    const mains = document.getElementById('mains');
    
    if (mains.getBoundingClientRect().top <= navId + 60) {
      if (ingredientsTab !== 'mains') {
        dispatch({
          type: INGREDIENTS_TAB_SWITCH,
          ingredient: 'mains'
        })
      }
    } else if (sauses.getBoundingClientRect().top <= navId + 60) {
      if (ingredientsTab !== 'sauses') {
        dispatch({
          type: INGREDIENTS_TAB_SWITCH,
          ingredient: 'sauses'
        })
      }
    } else {
      if (ingredientsTab !== 'buns') {
        dispatch({
          type: INGREDIENTS_TAB_SWITCH,
          ingredient: 'buns'
        })
      }
      return
    }
  }

  const getIngredientsList = (n) => {
    const arr = ingredients.filter((el) => el.type === n);
    return(arr);
  }
  return (
    <section className={styles.wrapper}>
      <h1 className={styles.headerSection}>Соберите бургер</h1>
      <IngredientsNavigation />
      <div className={styles.ingredients} onScroll={changeView}>
        <IngredientsType ingredientId={'buns'} header='Булки' ingredients={getIngredientsList('bun')}/>
        <IngredientsType ingredientId={'sauses'} header='Соусы' ingredients={getIngredientsList('sauce')}/>
        <IngredientsType ingredientId={'mains'} header='Начинки' ingredients={getIngredientsList('main')}/>
      </div>
    </section>
  )
}

IngredientsType.propTypes = {
  ingredientId: PropTypes.string.isRequired,
  header: PropTypes.string.isRequired,
  ingredients: PropTypes.arrayOf(ingredientsPropsTypes.isRequired).isRequired,
}

export default BurgerIngredients