import { InputForm } from '../../components/input-forms/input-form';
import { useDispatch, useSelector } from 'react-redux';
import { sendLoginUser } from '../../services/actions/user';
import { useHistory, Redirect } from 'react-router-dom';
import { inputReturns } from '../../utils/inputReturns';

export const LoginPage = () => {
  const userAuthorized = useSelector(state => state.user.userAuthorized);
  const dispatch = useDispatch();
  const history = useHistory();
  const state = useSelector(state => state.user.loginUserSuccess);
  const submit = (e) => {
    e.preventDefault();
    const { email, password, parentElementEmail, parentElementPassword } = inputReturns();
    if([...parentElementPassword.children].length === 2 || [...parentElementEmail.children].length === 2 || email === '' || password === '') {
      alert('Пожалуйста, введите корректные данные')
      return
    }
    dispatch(sendLoginUser(email, password))
  }
  if (state) {
    history.replace({pathname: '/'})
  }

  if (userAuthorized) {
    return (
      <Redirect
        to={{
          pathname: '/'
        }}
      />
    );
  }
  return (
    <InputForm
      title='Вход'
      email={{placeholder: 'E-mail'}}
      password={{placeholder: 'Пароль'}}
      btnName='Войти'
      btnSubmit={submit}
      redirectInfo={
        [
          {
            id: 1,
            question: 'Вы - новый пользователь?', 
            answer: 'Зарегистрироваться',
            link: '/register'
          },
          {
            id: 2,
            question: 'Забыли пароль?', 
            answer: 'Восстановить пароль',
            link: '/forgot-password'
          }
        ]
      }
    />
  )
}