import { CurrencyIcon, Button, ConstructorElement, DragIcon } from '@ya.praktikum/react-developer-burger-ui-components'
import OrderDetails from '../order-details/order-details'
import Modal from '../modal/modal';
import styles from './burger-constructor.module.css'
import { useDispatch, useSelector } from 'react-redux';
import { useCallback, useRef } from 'react'
import { sendOrder, CLOSE_MODAL_ORDER} from '../../services/actions/order'
import { useDrop, useDrag } from 'react-dnd'
import { addToConstructor, REMOVE_FROM_CONSTRUCTOR, SHIFT_INSIDE_CONSTRUCTOR } from '../../services/actions/burger-constructor';
import { useLocation, useHistory } from 'react-router-dom';

const BurgerBunContainer = ({type}) => {
  const dispatch = useDispatch();
  const { buns } = useSelector(state => state.burgerConstructor);
  const [{isHover}, dropTarget] = useDrop({
    accept: 'bun',
    collect: monitor => ({
      isHover: monitor.isOver()
    }),
    drop(item) {
      dispatch(addToConstructor(item))
    }
  })
  return (
    <>
      {
        !buns ? 
        <div ref={dropTarget} className={`${isHover ? styles.bunNotChosenOnHover : styles.bunNotChosen}`}>Перетащите сюда выбранный тип булок</div> :
        <div ref={dropTarget} className={styles.ingredientsBunUpper}>
          <ConstructorElement type={type} isLocked={true} text={`${buns.name} ${type === 'top' ? '(верх)': '(низ)'}`} thumbnail={buns.image_mobile} price={buns.price}/>
        </div>
      }
    </>
  )
}

const OrderedIngredient = ({item, index, moveCard}) => {
  const dispatch = useDispatch();
  const ref = useRef(null);
  const [{ handlerId }, drop] = useDrop({
    accept: 'component',
    collect(monitor) {
      return {
        handlerId: monitor.getHandlerId()
      }
    },
    hover(item, monitor) {
      if (!ref.current) {
        return;
      }
      const dragIndex = item.index;
      const hoverIndex = index;
      if (dragIndex === hoverIndex) {
        return;
      }
      const hoverBoundingRect = ref.current?.getBoundingClientRect();
      const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      const clientOffset = monitor.getClientOffset();
      const hoverClientY = clientOffset.y - hoverBoundingRect.top;
      if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
        return;
      }
      if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
        return;
      }
      moveCard(dragIndex, hoverIndex);
      item.index = hoverIndex;
    }
  })
  const [{ isDragging }, drag] = useDrag({
    type: 'component',
    item: () => ({ id: item.id, index }),
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });
  const opacity = isDragging ? 0 : 1;
  if (item.type !== 'bun') drag(drop(ref));
  const preventDefault = (e) => e.preventDefault();

  const handleClose = (item) => {
    dispatch({
      type: REMOVE_FROM_CONSTRUCTOR,
      item: item
    })
  }
  return (
    <div
      ref={ref}
      style={{ opacity }}
      onDrop={preventDefault}
      className={styles.ingredient}
      data-handler-id={handlerId}
      id={item.id}
    >
      <DragIcon type='primary'/>
      <ConstructorElement text={item.name} thumbnail={item.image_mobile} price={item.price} handleClose={(item) => handleClose(item)}/>
    </div>
  )
}

const BurgerRestIngredientsContainer = () => {
  const dispatch = useDispatch();
  const { restIngredients } = useSelector(state => state.burgerConstructor);

  const moveCard = useCallback((dragIndex, hoverIndex) => {
    const dragCard = restIngredients[dragIndex];
    const newCards = [...restIngredients]
    newCards.splice(dragIndex, 1)
    newCards.splice(hoverIndex, 0, dragCard)
    dispatch({
      type: SHIFT_INSIDE_CONSTRUCTOR,
      optional: newCards,
    })
  }, [restIngredients, dispatch]);

  const [{isHover}, dropTarget] = useDrop({
    accept: 'notBun',
    collect: monitor => ({
      isHover: monitor.isOver()
    }),
    drop(item) {
      dispatch(addToConstructor(item))
    }
  })

  return (
    <div ref={dropTarget} className={styles.ingredientsNotBuns}>
      {
        restIngredients.length === 0 ?
        <div className={`${isHover ? styles.restIngredientsNotChosenOnHover : styles.restIngredientsNotChosen}`}>А сюда передащите остальные ингредиенты</div> : 
        restIngredients.map((item, index) => 
          <OrderedIngredient key={item.id} index={index} item={item} moveCard={moveCard}/>
        )
      }
    </div>
  )
}

const BurgerConstructorList = () => {
  return (
    <section className={styles.section}>
      <BurgerBunContainer type='top'/>
      <BurgerRestIngredientsContainer />
      <BurgerBunContainer type='bottom'/>
    </section>
  )
}

const BurgerConstructorSummary = () => {
  const userAuthorized = useSelector(state => state.user.userAuthorized);
  const history = useHistory();
  const dispatch = useDispatch();
  const { buns, restIngredients} = useSelector(state => state.burgerConstructor)
  const { isModalOpened } = useSelector(state => state.sendOrder)

  const openModal = () => {
    if(!buns || restIngredients.length === 0) {
      alert('Пожалуйста, выберите тип хлеба и начинку!');
      return
    }
    if(!userAuthorized) {
      history.replace({pathname: '/login'})
      return
    }
    dispatch(sendOrder(buns, restIngredients))
  }

  const closeModal = (e) => {
    if (e.target === e.currentTarget) {
      dispatch({
        type: CLOSE_MODAL_ORDER,
      });
    }
    if (e === 'Escape' || e.target.id === 'modalOverlay' || e.currentTarget.id === 'modalCloseBtn')     
    dispatch({
      type: CLOSE_MODAL_ORDER,
    });
    return;
  }

  const totalCostEval = () => {
    let total;
    if (!buns) return <span>0</span>;
    if (restIngredients.length === 0) {
      total = buns.price * 2
      return <span>{total}</span>
    }
    total = buns.price * 2 + restIngredients.reduce((acc, el) => acc + el.price, 0)
    return <span>{total}</span>;
  }
  return (
    <div className={styles.summary}>
      <div className={styles.costs}>
        {totalCostEval()}
        <CurrencyIcon type='primary' />
      </div>
      <Button type='primary' size='medium' onClick={openModal} htmlType='button'>Оформить заказ</Button>
      {isModalOpened && (
      <Modal closeModal={closeModal}>
        <OrderDetails />
      </Modal>
      )}
    </div>
  )
}


const BurgerConstructor = () => {
  return (
    <section>
      <BurgerConstructorList />
      <BurgerConstructorSummary />
    </section>
  )
}

export default BurgerConstructor